.wrapper {
  z-index: 50;
  margin-left: 30%;
  margin-right: 30%;
  margin-top: 20%;
  margin-bottom: 20%;
  position: fixed;
  background-color: black;
  width: 40%;
  height: 100%;
  border-radius: 10px;
  padding: 5px;
  opacity: 0.6;
  align-content: center;
  display: flex;
  box-shadow: 0px 10px 50px #555;
}

.form-wrapper {
  width: 400px;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 20px 40px;
  border-radius: 10px;
  background-color: rgb(2, 2, 2);
}

.gamebox-title {
  color: white;
  display: flex;
}

.username,
.gamemode,
.noOfPlayers,
.commence-war {
  margin-left: 5%;
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
  width: 49%;
}

form {
  flex-wrap: wrap;
}

body {
  width: 100%;
  height: 100%;
  font-family: arial, verdana, sans-serif;
  font-weight: 700;
  color: black;
  background-color: white;
}

.map-sidebar-wrapper {
  z-index: 500;
  margin-left: 70%;
  margin-right: 0%;
  margin-top: 10%;
  margin-bottom: 0%;
  position: fixed;
  background-color: black;
  width: 30%;
  height: 90%;
  border-radius: 5px;
  padding: 5px;
  opacity: 0.6;
  align-content: center;
  box-shadow: 0px 10px 50px #555;
  text-align: center;
}

.map-wrapper {
  z-index: -500;
}

.map-sidebar-info-wrapper {
  color: white;
  display: flex;
  z-index: 600;
  align-content: center;
  display: flex;
  flex-direction: column;
  padding: 5%;
}

